import Vuex from 'vuex';
import Vue from "vue";
import {app} from "@/store/modules/app";
import {storePlugins} from "@/plugins/storePlugins";

Vue.use(Vuex);

export const store = new Vuex.Store({
  plugins: [storePlugins],
  modules: {
    app,
  },
  strict: process.env.NODE_ENV !== 'production',
});
