<template>
  <BContainer fluid="xl">
    <h1>Вход</h1>
<!--    <BButton @click="$store.dispatch('app/setAuthType', AUTH_TYPES.STUDENT)">Войти как студент</BButton>-->
<!--    <BButton @click="$store.dispatch('app/setAuthType', AUTH_TYPES.EMPLOYER)">Войти как работодатель</BButton>-->


    <BForm @submit="auth">
      <BFormGroup label="Email:" description="ttt1@gmail.com">
        <BFormInput v-model="form.email" required />
      </BFormGroup>
      <BFormGroup label="Пароль:" description="password">
        <BFormInput v-model="form.password" type="password" required />
      </BFormGroup>
      <BButton type="submit" :disabled="isPending" class="mr-2" variant="primary">Войти</BButton>
      <BButton type="button" disabled @click="$store.dispatch('app/setAuthType', AUTH_TYPES.NONE)">Выйти</BButton>
    </BForm>

  </BContainer>
</template>

<script>
import {AUTH_TYPES} from "@/store/modules/app";

export default {
  name: "TheLogin",
  data() {
    return {
      AUTH_TYPES,
      form: {
        email: 'ttt1@gmail.com',
        password: 'password',
      },
      isPending: false,
    }
  },
  methods: {
    auth(evt) {
      evt.preventDefault();
      this.isPending = true;

      this.$store.dispatch('app/login', this.form)
        .then(res => {
          console.log('login res', res)
        })
        .finally(() => {
          this.isPending = false;
        });
    }
  }
}
</script>

