import Vue from "vue";
import VueRouter from "vue-router";
import StudentGeneral from "@/views/StudentGeneral";
import StudentProfile from "@/views/StudentProfile";
import TheMain from "@/views/TheMain";
import TheMessages from "@/views/TheMessages";
import TheEmployer from "@/views/TheEmployer";
import TheLogin from "@/views/TheLogin";
import NotFound from "@/components/NotFound";
import {store} from "@/store";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: TheMain,
      meta: {
        layout: '',
      },
    },
    {
      path: '/student',
      component: StudentGeneral,
      meta: {
        layout: 'AppLayoutStudent',
      },
    },
    {
      path: '/profile',
      name: 'studentProfile',
      component: StudentProfile,
      meta: {
        layout: 'AppLayoutStudent',
        requiresAuth: true,
      },
    },
    {
      path: '/messages',
      name: 'messages',
      component: TheMessages,
      meta: {
        layout: 'AppLayoutStudent',
        // requiresAuth: true,
      },
    },
    {
      path: '/employer',
      component: TheEmployer,
      meta: {
        layout: 'AppLayoutEmployer',
      },
    },
    {
      path: '/login',
      component: TheLogin,
      meta: {
        layout: '',
      },
    },
    {
      path: '*',
      component: NotFound,
      meta: {},
    },
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters['app/isAuth']) {
      next();
      return;
    }
    // TODO Понять различия между 2мя вариантами. Оставить один
    // #1
    next('/login');
    // #2
    // next({
    //   path: '/login',
    //   query: { redirect: to.fullPath }
    // });
    return;
  }
  next();
})

export {router};
