import axios from "axios";
import {app} from "@/main";

class BaseApiService {
  baseUrl = `${process.env.VUE_APP_SCHEME}://${process.env.VUE_APP_HOST}/api/v1`;
  source = '';

  constructor(source) {
    if (!source) throw new Error("source is not provided");
    this.source = source;
  }

  getUrl(resource = '') {
    return `${this.baseUrl}/${this.source}/${resource}`;
  }

  handleErrors(err) {
    // Note: here you may want to add your errors handling
    console.warn({message: "Errors is handled here", err});
    app.$bvToast.toast('Произошла ошибка', {
      title: `Variant`,
      variant: 'danger',
      solid: true
    });
    throw  new Error(err);
  }
}

export class ModelApiService extends BaseApiService {
  constructor(source) {
    super(source);
  }

  /**
   * Получение одного
   * @param {string} resource
   * @returns {Promise<any>}
   */
  async get(resource = '') {
    try {
      const response = await axios({url: this.getUrl(resource), method: 'GET'})
      return response.data;
    } catch (err) {
      return this.handleErrors(err);
    }
  }

  /**
   * Создание
   * @param {string} resource
   * @param {Object} data
   * @returns {Promise<*>}
   */
  async post(resource = '', data = {}) {
    try {
      const response = await axios({url: this.getUrl(resource), data: data, method: 'POST'})
      return response.data;
    } catch (err) {
      this.handleErrors(err);
    }
  }

  /**
   * Обновление
   * @param {string} resource
   * @param {Object} data
   * @returns {Promise<*>}
   */
  async update(resource, data = {}) {
    if (!resource) throw Error("Resource is not provided");
    try {
      const response = await axios({url: this.getUrl(resource), data: data, method: 'POST'});
      return response.data;
    } catch (err) {
      this.handleErrors(err);
    }
  }

  /**
   * Удаление
   * @param {string} resource
   * @param {Object} data
   * @returns {Promise<boolean>}
   */
  async delete(resource, data = {}) {
    if (!resource) throw Error("Resource is not provided");
    try {
      const response = await axios({url: this.getUrl(`${resource}/delete`), data: data, method: 'POST'});
      return response.data;
    } catch (err) {
      this.handleErrors(err);
    }
  }
}
