import {ModelApiService} from "@/services/api";

export class AuthApiService extends ModelApiService {
  constructor() {
    super('auth');
  }

  /**
   * Авторизация
   * @param {string} email
   * @param {string} password
   * @returns {Promise<*>}
   */
  async login(email, password) {
    const response = await this.post('login', {
      email,
      password,
    });

    return response.token;
  }

  /**
   * Регистрация
   * @param {string} email
   * @param {string} password
   * @param {string} password_confirmation
   * @returns {Promise<*>}
   */
  async register(email, password, password_confirmation) {
    return await this.post('register', {
      email,
      password,
      password_confirmation,
    });
  }

  /**
   * Выход
   * @returns {Promise<*>}
   */
  async logout() {
    return await this.post('logout');
  }

  /**
   * Смена пароля
   * @param {string} current_password
   * @param {string} new_password
   * @param {string} new_password_confirmation
   * @returns {Promise<*>}
   */
  async changePassword(current_password, new_password, new_password_confirmation ) {
    return await this.post('changepassword', {
      current_password,
      new_password,
      new_password_confirmation,
    });
  }
}
