<template>
  <BContainer fluid="xl">
    <BRow>
      <!--ТАБЫ -->
      <BCol
        class="bg-dark p-3"
        cols="auto"
      >
        <MessagesTabType
          v-for="tab in tabs"
          :key="tab.type"
          :tabType="tab.type"
          :text="tab.text"
          :icon="tab.icon"
          class="mb-4"
        />
      </BCol>
      <!--ПРЕВЬЮ ДИАЛОГОВ-->
      <BCol
        class="bg-white p-0"
        cols="4"
      >
        <!--СТРОКА ПОИСКА-->
        <div class="p-3">
          <BInputGroup>
            <BFormInput
              type="search"
              placeholder="Поиск"
            />
            <BInputGroupAppend is-text>
              <BIcon icon="search"/>
            </BInputGroupAppend>
          </BInputGroup>
        </div>
        <!--ПРЕВЬЮ ДИАЛОГА-->
        <MessagesDialoguePreview
          v-for="item in dialogues"
          :key="item.id"
          :dialogue="item"
        />
      </BCol>
      <BCol col class="bg-light">
        <MessagesDialogue/>
      </BCol>
    </BRow>
  </BContainer>
</template>

<script>
import MessagesTabType from "@/components/MessagesTabType";
import MessagesDialoguePreview from "@/components/MessagesDialoguePreview";
import MessagesDialogue from "@/components/MessagesDialogue";

export default {
  name: "TheMessages",
  data() {
    return {
      TAB_TYPE,
      dialogues: [ // TODO Тут будет получение данных с сервака.
        {
          id: '1',
          from: 'Алексей',
          lastMessage: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Commodi consectetur, distinctio eos hic laborum laudantium minus mollitia quaerat, repellat sapiente sequi sit totam. Adipisci assumenda dicta eaque facilis in. Sed!',
          lastMessageDate: '22/06/2021 22:40',
          isRead: false,
          isActive: true,
        },
        {
          id: '2',
          from: 'Елена',
          lastMessage: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Itaque, molestias.',
          lastMessageDate: '22/06/2021 22:40',
          isRead: true,
          isActive: false,
        },
      ],
      tabs: [
        {
          type: TAB_TYPE.ALL,
          text: 'Все',
          icon: 'chat-dots',
        },
        {
          type: TAB_TYPE.READ,
          text: 'Прочитанные',
          icon: 'envelope-open',
        },
        {
          type: TAB_TYPE.UNREAD,
          text: 'Непрочитанные',
          icon: 'envelope',
        },
        {
          type: TAB_TYPE.FAVORITES,
          text: 'Избранные',
          icon: 'star',
        },
        {
          type: TAB_TYPE.ARCHIVE,
          text: 'Архив',
          icon: 'archive',
        },
      ]
    }
  },
  components: {
    MessagesTabType,
    MessagesDialoguePreview,
    MessagesDialogue,
  },
  beforeRouteUpdate: redirectToQuery,
  beforeRouteEnter: redirectToQuery,
  methods: {},
}

const TAB_TYPE = {
  ALL: 'all',
  READ: 'read',
  UNREAD: 'unread',
  FAVORITES: 'favorites',
  ARCHIVE: 'archive',
};

/**
 * Редиректит на общий query параметр, если его нет
 * @param to
 * @param from
 * @param next
 */
function redirectToQuery(to, from, next) {
  if (!to.query.activeTab) {
    next({
      path: to.fullPath,
      query: {
        activeTab: TAB_TYPE.ALL,
      }
    });
    return;
  }
  next();
}
</script>

<style lang="scss" scoped>
</style>
