<template>
  <component class="d-flex flex-column h-100" :is="layout">
<!--    <slot/>-->
    <router-view class="h-100"/>
  </component>
</template>

<script>
const defaultLayout = 'AppLayoutDefault'

export default {
  name: "AppLayout",
  computed: {
    layout() {
      const layout = this.$route.meta.layout || defaultLayout
      return () => import(`@/layouts/${layout}.vue`)
    }
  }
}
</script>

