/**
 * Сервис профиля студента
 */
export class StudentApiService extends ModelApiService {
  constructor() {
    super('profile');
  }

  /**
   * Получение профиля
   * @returns {Promise<*>}
   */
  async getProfile() {
    const response = await this.get();
    return response.data;
  }

  /**
   * Создание профиля
   * @param {ProfileParams} params
   * @returns {Promise<*>}
   */
  async createProfile(params) {
    const response =  this.post('create', params);
    return response.profile;
  }

  /**
   * Изменение профиля
   * @param {ProfileParams} params
   * @returns {Promise<*>}
   */
  async updateProfile(params) {
    return await this.post('update', params);
  }

  /**
   * Получение типов ссылок на соцсети
   * @returns {Promise<*>}
   */
  async getSocials() {
    const response = await this.get('social/types');
    return response.data;
  }

  /**
   * Создание ссылки на соцсеть
   * @param {string} link
   * @param {string} type_id
   * @returns {Promise<*>}
   */
  async createSocial(link, type_id) {
    const response =  await this.post('social/create', {
      link,
      type_id,
    });
    return response.data;
  }

  /**
   * Изменение ссылки на соцсеть
   * @param id
   * @param link
   * @param type_id
   * @returns {Promise<*>}
   */
  async updateSocial(id, link, type_id) {
    return await this.update('social', {
      id,
      link,
      type_id,
    });
  }

  /**
   * Удаление ссылки на соцсеть
   * @param {string} id
   * @returns {Promise<*>}
   */
  async deleteSocial(id) {
    return await this.delete('social', {id});
  }

  /**
   * Получить фотографию профиля
   * @returns {Promise<*>}
   */
  async getPhoto() {
    return await this.get('photo');
  }

  /**
   * Установить фотографию профиля
   * @param file
   * @returns {Promise<*>}
   */
  async setPhoto(file) {
    return await this.post('photo/set', {file});
  }

  /**
   * Удалить фотографию профиля
   * @returns {Promise<*>}
   */
  async deletePhoto() {
    return await this.delete('photo');
  }

  /**
   * Получение списка профессиональных сфер
   * @returns {Promise<*>}
   */
  async getSpheres() {
    return await this.post('skills/findspheres');
  }

  /**
   * Получение списка навыков
   * @returns {Promise<*>}
   */
  async getSkills() {
    // Возможны необязательные параметры
    // name - string
    // sphere_id - int
    // parent_id - int
    return await this.get('skills/find');
  }

  /**
   * Добавление навыка к профилю
   * @param id
   * @returns {Promise<*>}
   */
  async addSkill(id) {
    return await this.post('skills/add', {id});
  }

  /**
   * Удаление навыка из портфолио
   * @param id
   * @returns {Promise<*>}
   */
  async removeSkill(id) {
    return await this.post('skills/remove', {id});
  }
}
