/**
 * Типы авторизаций
 * @type {{STUDENT: string, EMPLOYER: string, NONE: string}}
 */
import axios from "axios";
import {$api} from "@/services";

const AUTH_TYPES = {
  STUDENT: 'student',
  EMPLOYER: 'employer',
  NONE: 'none',
}

/**
 * Обьект стостояния
 * @returns {{authType: AUTH_TYPES}}
 */
const stateObject = {
  authType: AUTH_TYPES.NONE,
}

//TODO это точно должно быть не тут
const token = localStorage.getItem('ureAuthToken');
token && (axios.defaults.headers.common['Authorization'] = `Bearer ${token}`);

const state = () => stateObject;

const getters = {
  /**
   * Авторизован пользовател или нет
   * @returns {boolean}
   */
  isAuth() {
    return !!localStorage.getItem('ureAuthToken');
    // return [AUTH_TYPES.EMPLOYER, AUTH_TYPES.STUDENT].includes(state.authType);
  }
}

const actions = {
  /**
   * Задает тип авторизации
   * @param {Function} commit
   * @param {AUTH_TYPES} type
   */
  setAuthType({ commit }, type) {
    commit('setAuthType', type);
  },
  async login({commit}, user) {
    try {
      const token = await $api.auth.login(user.email, user.password)
      localStorage.setItem('ureAuthToken', token);
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      commit('auth_success', token, user);
    } catch (err) {
      commit('auth_error')
      localStorage.removeItem('ureAuthToken');
    }
  },
}

const mutations = {
  /**
   * Задает тип авторизации
   * @param {stateObject} state
   * @param {AUTH_TYPES} type
   */
  setAuthType(state, type) {
    state.authType = type;
  },
}

const app = {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}

export { app, AUTH_TYPES };
