<template>
  <BCard :class="{'bg-info': isColored}">
    <h4>{{getText('headings')}}</h4>
    <div v-if="cardData && cardData.length">
      <component :is="cardTemplate" :cardData="cardData"></component>
    </div>
    <div v-else>
      {{getText('zeroData')}}
      <br>
      <BButton>Добавить</BButton>
    </div>
  </BCard>
</template>

<script>
export default {
  name: "StudentsProfileCard",
  props: {
    isColored: {
      type: Boolean,
      default: false,
      required: false,
    },
    card: {
      type: String,
      required: true,
    },
    cardData: {
      type: Array,
      required: true,
    }
  },
  data: () => {
    return {
      headings: {
        education: 'Образование',
        skills: 'Навыки',
        job: 'Опыт работы',
        courses: 'Курсы',
        aims: 'Карьерные цели',
      },
      zeroData: {
        education: 'Расскажите о вашем образовании.',
        skills: 'Расскажите о своих навыках.',
        job: 'Расскажите о вашем опыте работы.',
        courses: 'Расскажите о пройденных вами курсах.',
        aims: 'Расскажите про свои карьерные цели.',
      }
    }
  },
  computed: {
    cardTemplate() {
      return () => import(`@/components/StudentsProfileCard${this.capitalize(this.card)}.vue`)
    },
  },
  methods: {
    capitalize(s) {
      return s && s[0].toUpperCase() + s.slice(1);
    },
    getText(place = '') {
      return place ? this[place][this.card] : '';
    },
  }
}
</script>

